/* ==================== CSS VARIABLES ==================== */
:root {
  --bg: #3c3c3c;
  --bg-card: #303030;
  --bg-panel: #262626;
  --border-subtle: rgba(120, 120, 120, 0.7);
  --accent: #e5e5e5;
  --accent-soft: rgba(229, 229, 229, 0.08);
  --text-main: #f5f5f5;
  --text-muted: #c4c4c4;
  --danger: #ff4b4b;
  --success: #4ade80;
  --sidebar-base: 240px;
  --header-height: 90px;
  --landscape-leftbar-width: 44px;
}

/* Light Theme Variables */
:root[data-theme="light"] {
  --bg: #f3f4f6;
  --bg-card: #ffffff;
  --bg-panel: #f9fafb;
  --border-subtle: rgba(148, 163, 184, 0.7);
  --accent: #111111;
  --accent-soft: rgba(17, 17, 17, 0.04);
  --text-main: #111111;
  --text-muted: #6b7280;
  --danger: #dc2626;
  --success: #16a34a;
}

/* ==================== GLOBAL STYLES ==================== */

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  background-color: #1f1f1f; /* SOLID color first for iOS status bar */
  background-image: radial-gradient(circle at top, #2f2f2f 0, #2a2a2a 55%, #1f1f1f 100%); /* Gradient on top */
  color: var(--text-main);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "SF Pro Text",
    "Segoe UI", sans-serif;
  transition: background 0.2s ease, color 0.2s ease;
}

:root[data-theme="light"] body {
  background-color: #f3f4f6; /* SOLID color for iOS status bar */
  background-image: radial-gradient(circle at top, #e5e7eb 0, #e5e7eb 50%, #d1d5db 100%);
}

/* ==================== WEBAPP STATUS BAR THEMING ==================== */

/* Theme-aware status bar background for portrait webapp mode */
html {
  background: #1f1f1f; /* Dark theme default */
}

:root[data-theme="light"] html {
  background: #f3f4f6; /* Light theme */
}

/* ==================== APP SHELL LAYOUT ==================== */

.app-shell {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: 1fr;
  grid-template-areas:
    "header"
    "main";
  min-height: 100vh;
}

/* ==================== SLIDE-UP FADE ANIMATION ==================== */

@keyframes slideUpFade {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Initial load animations with staggered delays (uniform 75ms intervals) */
.header-logo-img {
  animation: slideUpFade 0.4s ease-out;
  animation-delay: 0s;
  animation-fill-mode: both;
}

.header-banner-img {
  animation: slideUpFade 0.4s ease-out;
  animation-delay: 0.075s;
  animation-fill-mode: both;
}

.header-tagline {
  animation: slideUpFade 0.4s ease-out;
  animation-delay: 0.15s;
  animation-fill-mode: both;
}

/* Theme toggle animations (same timing as initial load) */
.header-logo-img.animate {
  animation: slideUpFade 0.4s ease-out;
  animation-delay: 0s;
  animation-fill-mode: both;
}

.header-banner-img.animate {
  animation: slideUpFade 0.4s ease-out;
  animation-delay: 0.075s;
  animation-fill-mode: both;
}

.header-tagline.animate {
  animation: slideUpFade 0.4s ease-out;
  animation-delay: 0.15s;
  animation-fill-mode: both;
}

/* ==================== HEADER SECTION ==================== */

.app-header {
  grid-area: header;
  position: sticky;
  top: 0;
  z-index: 40;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  background: #1f1f1f;
  border-bottom: 1px solid rgba(148, 148, 148, 0.6);
  min-height: var(--header-height);
}

:root[data-theme="light"] .app-header {
  background: #f3f4f6;
  border-bottom-color: rgba(148, 163, 184, 0.5);
}

.header-bar {
  width: 100%;
  max-width: 1100px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  column-gap: 16px;
}

/* Sidebar Toggle Button */
.sidebar-toggle {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  border: none;
  background: transparent;
  color: #e5e5e5;
  cursor: pointer;
  padding: 4px 2px;
  height: 32px;
}

:root[data-theme="light"] .sidebar-toggle {
  color: #111111;
}

.sidebar-toggle-icon {
  width: 18px;
  height: 2px;
  background: currentColor;
  border-radius: 999px;
  position: relative;
  transition: background 0.15s ease;
  flex-shrink: 0;
}

.sidebar-toggle-icon::before,
.sidebar-toggle-icon::after {
  content: "";
  position: absolute;
  left: 0;
  width: 18px;
  height: 2px;
  background: currentColor;
  border-radius: 999px;
  transition: transform 0.15s ease, opacity 0.15s ease, top 0.15s ease,
    bottom 0.15s ease;
}

.sidebar-toggle-icon::before {
  top: -5px;
}

.sidebar-toggle-icon::after {
  bottom: -5px;
}

.sidebar-toggle[data-open="true"] .sidebar-toggle-icon {
  background: transparent;
}

.sidebar-toggle[data-open="true"] .sidebar-toggle-icon::before {
  top: 0;
  transform: rotate(45deg);
}

.sidebar-toggle[data-open="true"] .sidebar-toggle-icon::after {
  bottom: 0;
  transform: rotate(-45deg);
}

.sidebar-toggle-label {
  font-size: 0.8rem;
  color: var(--text-muted);
  white-space: nowrap;
}

@media (max-width: 640px) {
  .sidebar-toggle-label {
    display: none;
  }
}

/* Logo and Banner */
.header-center {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 14px;
}

.header-logo-wrap {
  height: 72px;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-banner-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
  height: 72px;
}

.header-logo-img {
  max-height: 100%;
  height: 72px;
  width: auto;
  display: block;
}

.header-banner-img {
  max-height: 56px;
  height: auto;
  width: auto;
  display: block;
}

.header-tagline {
  font-family: 'Uni Sans', sans-serif;
  font-size: 0.7rem;
  font-weight: 400;
  color: var(--text-muted);
  margin: 0;
  padding: 0;
  line-height: 1.2;
  white-space: nowrap;
  text-align: center;
}

@media (max-width: 640px) {
  .app-header {
    padding: 8px 12px;
    min-height: 60px;
  }

  .header-logo-wrap {
    height: 44px;
  }

  .header-logo-img {
    height: 44px;
  }

  .header-banner-wrap {
    display: none;
  }
}

/* Theme Toggle Button */
.theme-toggle {
  justify-self: end;
  width: 36px;
  height: 36px;
  border-radius: 999px;
  border: 1px solid var(--border-subtle);
  display: flex;
  align-items: center;
  justify-content: center;
  background: #262626;
  cursor: pointer;
  color: #f5f5f5;
  transition: background 0.2s ease, border-color 0.2s ease, transform 0.1s ease;
}

:root[data-theme="light"] .theme-toggle {
  background: #e5e7eb;
  color: #111111;
}

.theme-toggle:hover {
  transform: translateY(-1px);
}

.theme-toggle span {
  font-size: 1rem;
  line-height: 1;
}

/* ==================== MOBILE LANDSCAPE LEFT BAR ==================== */

.landscape-leftbar {
  display: none;
}

@media (max-height: 600px) and (orientation: landscape) {
  /* Fixed viewport dimensions */
  html, body {
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
  }

  /* Body background extends into safe areas */
  body {
    background: radial-gradient(circle at top, #2f2f2f 0, #2a2a2a 55%, #1f1f1f 100%) !important;
  }
  
  :root[data-theme="light"] body {
    background: radial-gradient(circle at top, #e5e7eb 0, #e5e7eb 50%, #d1d5db 100%) !important;
  }

  .app-shell {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
  }

  /* Notch area blocker - covers notch with solid color */
  body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: env(safe-area-inset-left, 0px);
    height: 100vh;
    background: #1f1f1f;
    z-index: 30;
    pointer-events: none;
  }

  :root[data-notch-side="right"] body::before {
    left: auto;
    right: 0;
    width: env(safe-area-inset-right, 0px);
  }

  :root[data-notch-side="none"] body::before {
    display: none;
  }

  :root[data-theme="light"] body::before {
    background: #f3f4f6;
  }

  /* Left bar - positioned based on notch detection */
  .landscape-leftbar {
    position: fixed;
    top: env(safe-area-inset-top, 0px);
    left: env(safe-area-inset-left, 0px);
    width: var(--landscape-leftbar-width);
    height: calc(100vh - env(safe-area-inset-top, 0px));
    z-index: 50;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 4px 0;
    padding-bottom: env(safe-area-inset-bottom, 0px);
    background: #1f1f1f;
    border-right: 1px solid rgba(148, 148, 148, 0.6);
  }

  /* Left bar when notch is on RIGHT or no notch detected */
  :root[data-notch-side="right"] .landscape-leftbar,
  :root[data-notch-side="none"] .landscape-leftbar {
    left: 0;
  }

  :root[data-theme="light"] .landscape-leftbar {
    background: #f3f4f6;
    border-right-color: rgba(148, 163, 184, 0.6);
  }

  .landscape-leftbar-toggle {
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
    color: var(--text-main);
    flex-shrink: 0;
  }

  .landscape-leftbar-logo {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px;
    width: 100%;
    overflow: hidden;
  }

  .landscape-leftbar-logo img {
    max-width: 38px;
    max-height: 38px;
    width: auto;
    height: auto;
    object-fit: contain;
    animation: slideUpFade 0.4s ease-out;
    animation-delay: 0s;
    animation-fill-mode: both;
  }

  .landscape-leftbar-logo img.animate {
    animation: slideUpFade 0.4s ease-out;
    animation-delay: 0s;
    animation-fill-mode: both;
  }

  .landscape-leftbar .theme-toggle {
    width: 32px;
    height: 32px;
    min-width: 32px;
    min-height: 32px;
    flex-shrink: 0;
  }

  .landscape-leftbar .theme-toggle span {
    font-size: 0.9rem;
  }

  /* Visual debug indicator (optional - remove in production) */
  .landscape-leftbar::before {
    content: 'LEFT';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 8px;
    color: rgba(0, 0, 255, 0.3);
    pointer-events: none;
    z-index: -1;
  }

  :root[data-notch-side="right"] .landscape-leftbar::before {
    content: 'RIGHT';
    color: rgba(255, 0, 0, 0.3);
  }

  :root[data-notch-side="none"] .landscape-leftbar::before {
    content: 'NONE';
    color: rgba(0, 255, 0, 0.3);
  }

  .app-header {
    display: none !important;
  }

  /* Sidebar - DEFAULT (notch on left) */
  .sidebar {
    position: fixed !important;
    top: env(safe-area-inset-top, 0px) !important;
    left: calc(var(--landscape-leftbar-width) + env(safe-area-inset-left, 0px)) !important;
    height: calc(100vh - env(safe-area-inset-top, 0px)) !important;
    width: 80vw !important;
    max-width: 360px !important;
    padding-bottom: env(safe-area-inset-bottom, 0px) !important;
    transform: translateX(-100%) !important;
    z-index: 20 !important;
    overflow: hidden !important;
  }

  /* Sidebar - notch on RIGHT or no notch */
  :root[data-notch-side="right"] .sidebar,
  :root[data-notch-side="none"] .sidebar {
    left: var(--landscape-leftbar-width) !important;
  }

  .sidebar[data-open="true"] {
    transform: translateX(0) !important;
  }

  /* Main content - DEFAULT (notch on left) */
  .main-content {
    position: fixed !important;
    top: env(safe-area-inset-top, 0px) !important;
    left: calc(var(--landscape-leftbar-width) + env(safe-area-inset-left, 0px)) !important;
    right: 0 !important;
    bottom: 0 !important;
    width: auto !important;
    margin: 0 !important;
    padding: 4px 8px !important;
    padding-bottom: calc(8px + env(safe-area-inset-bottom, 0px)) !important;
    overflow-y: auto;
    overflow-x: hidden;
    background: transparent !important;
  }

  /* Main content - notch on RIGHT */
  :root[data-notch-side="right"] .main-content {
    left: var(--landscape-leftbar-width) !important;
    right: env(safe-area-inset-right, 0px) !important;
  }

  /* Main content - no notch */
  :root[data-notch-side="none"] .main-content {
    left: var(--landscape-leftbar-width) !important;
    right: 0 !important;
  }

  .main-content .page-root {
    padding: 2px 2px 12px !important;
    max-width: 100%;
    background: transparent !important;
  }

  .page-root .card {
    margin-top: 4px !important;
    padding: 10px 16px 18px !important;
    border-radius: 14px !important;
    max-width: 100%;
  }

  .card .content-header,
  .card .calculator-header {
    margin-bottom: 12px !important;
  }
}

/* ==================== SIDEBAR NAVIGATION ==================== */

.sidebar {
  position: fixed;
  top: var(--header-height);
  left: 0;
  z-index: 30;
  height: calc(100vh - var(--header-height));
  padding: 12px 10px 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  border-right: 1px solid rgba(148, 148, 148, 0.6);
  background: radial-gradient(
    circle at top left,
    rgba(31, 31, 31, 0.96),
    rgba(31, 31, 31, 0.9)
  );
  backdrop-filter: blur(18px);
  transform: translateX(-100%);
  overflow: hidden;
  transition: transform 0.22s ease-out;
}

:root[data-theme="light"] .sidebar {
  background: radial-gradient(circle at top left, #f9fafb, #e5e7eb);
  border-right-color: rgba(148, 163, 184, 0.6);
}

@media (max-width: 640px) {
  .sidebar {
    top: 60px;
    height: calc(100vh - 60px);
  }
}

/* Sidebar Header (Breadcrumb) */
.sidebar-header {
  font-size: 0.78rem;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: var(--text-muted);
  padding: 4px 4px 4px 6px;
}

.sidebar-breadcrumb {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 4px;
  font-size: 0.78rem;
  text-transform: none;
  letter-spacing: 0;
}

.sidebar-breadcrumb-part {
  cursor: pointer;
  color: var(--text-muted);
  transition: color 0.15s ease;
}

.sidebar-breadcrumb-part.current {
  cursor: default;
  color: var(--text-main);
}

.sidebar-breadcrumb-separator {
  color: var(--text-muted);
}

/* Sidebar Navigation Menu */
.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding-right: 4px;
  overflow-y: auto;
}

.nav-item {
  border-radius: 0;
  padding: 5px 6px;
  font-size: 0.88rem;
  color: var(--text-muted);
  cursor: pointer;
  display: flex;
  align-items: flex-start;
  gap: 0px;
  justify-content: flex-start;
  border: none;
  background: transparent;
  transition: background 0.18s ease, color 0.18s ease;
}

.nav-index {
  font-size: 0.78rem;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  color: var(--text-muted);
  min-width: 24px;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
  align-self: flex-start;
}

.nav-icon {
  font-size: 0.85rem;
  color: var(--text-muted);
  opacity: 0.7;
  margin-left: 4px;
  margin-right: 6px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  min-width: 14px;
  justify-content: center;
  align-self: center;
}

.nav-label {
  font-size: 1.2rem;
  line-height: 1.1;
  color: var(--text-main);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  align-self: center;
}

.nav-item:hover .nav-index,
.nav-item.active .nav-index {
  color: rgb(224, 160, 31);
}

.nav-item:hover .nav-icon {
  opacity: 1;
}

.nav-item:hover {
  background: rgba(80, 80, 80, 0.35);
}

:root[data-theme="light"] .nav-item:hover {
  background: rgba(148, 163, 184, 0.25);
}

/* Sidebar Footer */
.sidebar-footer {
  margin-top: auto;
  font-size: 0.75rem;
  color: var(--text-muted);
  padding: 12px 4px 0 6px;
}

/* Sidebar Responsive Behavior */
@media (min-width: 841px) {
  .sidebar[data-open="true"] {
    transform: translateX(0);
    transition: transform 0.22s ease-out, width 0.2s ease-out;
  }
}

@media (max-width: 840px) {
  .sidebar {
    width: 80vw;
    max-width: 360px;
  }

  .sidebar[data-open="true"] {
    transform: translateX(0);
  }
}

/* ==================== SEARCH BAR SECTION ==================== */

.sidebar-search {
  padding: 8px 10px 12px;
  border-bottom: 1px solid rgba(148, 148, 148, 0.3);
}

:root[data-theme="light"] .sidebar-search {
  border-bottom-color: rgba(148, 163, 184, 0.3);
}

.search-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  gap: 6px;
  background: #262626;
  border: 1px solid rgba(180, 180, 180, 0.5);
  border-radius: 8px;
  padding: 6px 8px;
  transition: border-color 0.2s ease, background 0.2s ease;
}

:root[data-theme="light"] .search-input-wrapper {
  background: #ffffff;
  border-color: rgba(148, 163, 184, 0.6);
}

.search-input-wrapper:focus-within {
  border-color: var(--accent);
  background: #2c2c2c;
}

:root[data-theme="light"] .search-input-wrapper:focus-within {
  background: #f9fafb;
}

.search-icon {
  font-size: 0.9rem;
  color: var(--text-muted);
  flex-shrink: 0;
  opacity: 0.7;
}

.search-input {
  flex: 1;
  border: none;
  background: transparent;
  color: var(--text-main);
  font-size: 0.88rem;
  outline: none;
  padding: 0;
  min-width: 0;
}

.search-input::placeholder {
  color: var(--text-muted);
  opacity: 0.6;
}

.search-clear {
  flex-shrink: 0;
  width: 18px;
  height: 18px;
  border: none;
  background: transparent;
  color: var(--text-muted);
  font-size: 0.75rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 0;
  transition: background 0.15s ease, color 0.15s ease;
}

.search-clear:hover {
  background: rgba(148, 148, 148, 0.2);
  color: var(--text-main);
}

.search-results {
  margin-top: 8px;
  max-height: 320px;
  overflow-y: auto;
  border-radius: 8px;
  background: #1f1f1f;
  border: 1px solid rgba(180, 180, 180, 0.4);
}

:root[data-theme="light"] .search-results {
  background: #ffffff;
  border-color: rgba(148, 163, 184, 0.5);
}

.search-result-item {
  padding: 8px 10px;
  cursor: pointer;
  border-bottom: 1px solid rgba(148, 148, 148, 0.2);
  transition: background 0.15s ease;
}

.search-result-item:last-child {
  border-bottom: none;
}

.search-result-item:hover,
.search-result-item.selected {
  background: rgba(80, 80, 80, 0.35);
}

:root[data-theme="light"] .search-result-item:hover,
:root[data-theme="light"] .search-result-item.selected {
  background: rgba(148, 163, 184, 0.15);
}

.search-result-title {
  font-size: 0.9rem;
  color: var(--text-main);
  margin-bottom: 3px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.search-result-icon {
  font-size: 0.75rem;
  opacity: 0.7;
}

.search-result-path {
  font-size: 0.75rem;
  color: var(--text-muted);
  opacity: 0.8;
}

.search-no-results {
  padding: 12px 10px;
  text-align: center;
  font-size: 0.85rem;
  color: var(--text-muted);
}

.search-match {
  background: rgba(224, 160, 31, 0.25);
  font-weight: 500;
  border-radius: 2px;
  padding: 0 2px;
}

:root[data-theme="light"] .search-match {
  background: rgba(224, 160, 31, 0.2);
}

/* ==================== MAIN CONTENT AREA ==================== */

.main-content {
  grid-area: main;
  padding: 16px;
  display: flex;
  justify-content: center;
}

.page-root {
  max-width: 980px;
  width: 100%;
  padding: 8px 4px 24px;
}

.card {
  position: relative;
  background: radial-gradient(
    circle at top left,
    var(--bg-card),
    var(--bg-panel) 45%,
    #1a1a1a 100%
  );
  border-radius: 18px;
  border: 1px solid var(--border-subtle);
  padding: 28px 32px 30px;
  margin-top: 16px;
  box-shadow: 0 18px 30px rgba(0, 0, 0, 0.7),
    0 0 0 1px rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(16px);
}

:root[data-theme="light"] .card {
  background: radial-gradient(
    circle at top left,
    var(--bg-card),
    var(--bg-panel) 40%,
    #e5e7eb 100%
  );
  box-shadow: 0 18px 30px rgba(148, 163, 184, 0.35),
    0 0 0 1px rgba(148, 163, 184, 0.3);
}

/* Card animation using slideUpFade keyframes */
.card.preload {
  animation: none;
  opacity: 0;
  transform: translateY(12px);
}

.card.loaded {
  animation: slideUpFade 0.4s ease-out 0s;
  animation-fill-mode: both;
}

/* ==================== RESPONSIVE ADJUSTMENTS ==================== */

/* General mobile styles (all orientations) */
@media (max-width: 480px) {
  .sidebar-header {
    font-size: 0.72rem;
  }
}

/* ============================================
   PORTRAIT MOBILE MODE - MAXIMIZE HORIZONTAL SPACE
   ============================================ */
@media (max-width: 600px) and (orientation: portrait) {
  /* Remove horizontal padding from main content area */
  .main-content {
    padding: 0.5rem 0; /* Vertical preserved, horizontal removed */
  }

  .page-root {
    padding: 0.5rem 0.25rem 1rem; /* Minimal horizontal */
  }

  /* Content cards: Moderate padding reduction */
  .card {
    margin: 0.5rem 0.25rem; /* ~8px vertical, 4px horizontal */
    padding: 18px 14px 20px; /* Reduced from 28px 32px 30px */
    border-radius: 14px; /* Slightly reduced */
  }

  /* Sidebar: Moderate horizontal padding reduction */
  .sidebar {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}